<template>
    <div>
        <div class="todo-item" :class="{'is-completed': todo.completed}">
            <input type="checkbox" v-on:change="markCompleted" v-bind:checked="todo.completed"/>
            <p> {{ todo.title }}</p>
            <button v-on:click="$emit('del-todo', todo.id)">Delete</button>
        </div>
    </div>
</template>

<script>
export default {
    name: 'TodoItem',
    props: {
        todo: Object
    },
    methods: {
        markCompleted() {
            this.todo.completed = !this.todo.completed
        }
    }
}
</script>

<style scoped>
    .todo-item {
        background: lightcoral;
    }
    .is-completed {
        background: lightgreen;
    }
</style>